<template>
  <div>
    <!-- <p>
      Your score was {{this.score}} while the average score is {{Math.floor(this.stats.avgscore * 100)}}.
    </p> -->
    <section class="container">
      <h1>
        <span class="title">Game</span>
        <br />
        <span class="title">Over</span>
      </h1>
      <br />
      <br />
      <br />
      <h1>
        Your score was {{this.score}}
        <br />
        The average global score is {{Math.floor(this.stats.avgscore * 100)}}
      </h1>
      <a class="twitter-share-button"
        :href="'https://twitter.com/intent/tweet?text=' + 'Yay! got a score of ' + this.score + ' on'"
        data-size="large"
      >
        Tweet
      </a>
      <h2>Your other scores this session are:</h2>
      <h3 v-for="(score, index) in this.history" :key="`score-${index}`">{{score}}</h3>
    </section>
  </div>
</template>

<script>
import api from "@/api";
import gameOverSound from '../assets/gameover.wav'

export default {
  props: {
    score: {
      type: Number,
      required: true
    },
    history: {
      type: Array,
    },
  },
  data() {
    return {
      stats: {
        avgscore: 0,
        avgduration: 0,
      },
    };
  },
  async created() {
    const response = await api.getStats();
    this.stats = response;
    (new Audio(gameOverSound)).play();
  },
  async mounted() {
    let twitterScript = document.createElement('script')
    twitterScript.setAttribute('src', 'https://platform.twitter.com/widgets.js')
    twitterScript.setAttribute('charset', 'utf-8')
    twitterScript.setAttribute('async', '')
    document.head.appendChild(twitterScript)

    // $(document).ready(function() {
    //   $(".title").lettering();
    //   $(".button").lettering();
    // });

    // $(document).ready(function() {
    //   animation();
    // }, 1000);

    // $(".button").click(function() {
    //   animation();
    // });

    // function animation() {
    //   var title1 = new TimelineMax();
    //   title1.to(".button", 0, { visibility: "hidden", opacity: 0 });
    //   title1.staggerFromTo(
    //     ".title span",
    //     0.5,
    //     { ease: Back.easeOut.config(1.7), opacity: 0, bottom: -80 },
    //     { ease: Back.easeOut.config(1.7), opacity: 1, bottom: 0 },
    //     0.05
    //   );
    //   title1.to(".button", 0.2, { visibility: "visible", opacity: 1 });
    // }
  }
};
</script>

<style lang="scss" scoped>
@import url(https://fonts.googleapis.com/css?family=Fjalla+One);

$background: linear-gradient(to bottom, #405166 0%, #656f6f 100%);
$red: #e55643;
$green: #2b9f5e;
$yellow: #f1c83c;
$shadow: #533d4a;

html {
  height: 100%;
}

body {
  font-family: "Fjalla One", sans-serif;
  background: $background;
}

.container {
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  display: block;
  position: absolute;
  // max-width: 525px;
}

.button {
  float: left;
  position: relative;
  bottom: -65px;
  left: 50%;
  transform: translateX(-50%) rotate(-10deg);
  color: $red;
  text-transform: uppercase;
  opacity: 0;
  visibility: hidden;
  cursor: pointer;

  span {
    transform: skew(-10deg);
    display: block;
    float: left;
    text-shadow: $shadow 1px 1px, $shadow 2px 2px, $shadow 3px 3px,
      $shadow 4px 4px;
  }
}

h1 {
  color: #000;
  text-transform: uppercase;
  font-size: 42px;
  margin: 0;
  line-height: 47px;
  letter-spacing: 2px;
}

.title {
  transform: translateX(-50%) rotate(-10deg);
  display: block;
  float: left;
  left: 50%;
  position: relative;

  span {
    transform: skew(-10deg);
    display: block;
    float: left;
    text-shadow: $shadow 1px 1px, $shadow 2px 2px, $shadow 3px 3px,
      $shadow 4px 4px, $shadow 5px 5px, $shadow 6px 6px;
    min-width: 10px;
    min-height: 10px;
    position: relative;
  }
}

.title {
  &:nth-child(1) {
    color: $red;
  }
  &:nth-child(2) {
    color: $green;
  }
  &:nth-child(3) {
    color: $yellow;
  }
}
</style>

